import { createSlice } from "@reduxjs/toolkit";
import data from "./data";

const colorSlice = createSlice({
    name: 'color',
    initialState: {
        color: data[0]
    },
    reducers: {
        toggle(state, action){
            state.color = data[action.payload]
        }
    }
})
export const {toggle} = colorSlice.actions
export default colorSlice.reducer
