import React from 'react'
import { useSelector } from 'react-redux'
import { RootState } from '../../store/store'
import style from './Header.module.css'

const Header = () => {
  const colors = useSelector((state: RootState) => state.colors.color)
  return (
    <div className={`${style.grid} positionHeader`}>
      <svg width="30" viewBox="0 0 51 55" fill="none" xmlns="http://www.w3.org/2000/svg">
        <rect width="37.5229" height="9.07637" transform="matrix(0.6468 0.762659 -0.784782 0.619771 23.9702 1.93091)" fill={colors[0]} />
        <rect width="37.5229" height="9.07637" transform="matrix(0.6468 0.762659 -0.784782 0.619771 16.8716 10.2505)" fill={colors[1]} />
        <rect width="37.5229" height="9.07637" transform="matrix(0.6468 0.762659 -0.784782 0.619771 9.77319 18.5702)" fill={colors[2]} />
      </svg>
      <div className={style.routes}>
        <div className='middleText'>ABOUT</div>
        <div className='middleText'>GALLERY</div>
        <div className='middleText'>CONTACTS</div>
      </div>
    </div>
  )
}

export default Header