import React from 'react'
import SwiperCore, { Autoplay, Keyboard, Mousewheel, Pagination } from 'swiper';
import Container from '../components/Content/Container/Container'
import TextInfo from '../components/Content/Text/TextInfo'
import Fog from '../components/Fog/Fog'
import Header from '../components/Header/Header'
import Metaball from '../components/Metaball/Metaball'
import Preloader from '../components/Preloader/Preloader';

const Main = () => {
    SwiperCore.use([Keyboard, Mousewheel, Autoplay, Pagination])
  return (
    <>
    <Preloader/>
    <Header/>
    <TextInfo/>
    <Container/>
    <Fog/>
    <Metaball/>
    </>
  )
}

export default Main