import React, { FC } from 'react'
import style from './God.module.css'

const God:FC<{name:string}> = ({name}) => {
    const {box} = style
  return (
    <div className={box}>
    <img src={`./todorchuk-offer/${name}.webp`} width={'auto'} height={'100%'} alt="" />
       </div>   
       
  )
}

export default God