import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import { Pagination } from 'swiper';
import 'swiper/css/pagination';
import God from './God/God';
import ContainerChange from './ContainerChange';
import { useDispatch } from 'react-redux';
import { toggle } from '../../../store/colorSlice';

const Container = () => {
  const dispatch = useDispatch()
  return (
    <>        
        <Swiper
        modules={[Pagination]}
        onSlideNextTransitionStart={()=>dispatch(toggle(ContainerChange()))}
        onSlidePrevTransitionStart={()=>dispatch(toggle(ContainerChange()))}
                    direction={'horizontal'}
                    mousewheel
                    keyboard={{enabled: true}}
                    loop
                    shortSwipes
                    pagination={{type:'progressbar'}}
                    autoplay={{delay: 3000, disableOnInteraction: false}}
                >
                   {['medusa1','poseidon1','zeus'].map((g)=><SwiperSlide key={g}><God name={g} /></SwiperSlide>)}
                </Swiper>
    </>
  )
}

export default Container