import React from 'react';
import Main from './pages/Main';

class App extends React.Component {
  componentDidMount(){
    const loader = document.querySelector('.loader')!
    loader.classList!.add('hide')
    setTimeout(()=>{
      loader.remove()
    }, 600)
  }
  render(): React.ReactNode {
    return (
     <>
      <Main/>
     </>
    );
  }
}

export default App;
