import anime from "animejs";

window.addEventListener('load', () => {
    const path1 = anime.path('#way1')
    const path2 = anime.path('#way2')
    function timeline() {
        const timeline = anime.timeline({
            duration: 6000,
            easing: "linear",
            loop: true,
        })
        return timeline
    }
    console.log('he')
    timeline().add({
        targets: `#meta1`,
        translateY: path1('y'),
        translateX: path1('x'),
    })
    timeline().add({
        targets: `#meta2`,
        translateY: path2('y'),
        translateX: path2('x'),
    })
})