import React from 'react'
import style from './TextInfo.module.css'
import {ReactComponent as ButtonSVG} from '../../../assets/Button.svg'
import { useSelector } from 'react-redux'
import { RootState } from '../../../store/store'

const TextInfo = () => {
  const name = useSelector((state: RootState) => state.colors.color)
  return (
    <div className={`${style.block} positionText`}>
    <div className={style.grid}>
      <div className={`${style.subhead} minorText`}>SCULPTURE</div>
      <div className={`${style.head} majorText`}>{name[3]}</div>
      <div className={`${style.subhead} minorText`}>Our hobby is a modern and convenient design,<br/>
        the key to successful communication with the<br/>
        client</div>
        <div className={`${style.box} middleText`}>
          <ButtonSVG/>
          <span>See More</span>
        </div>
    </div>
    </div>
  )
}

export default TextInfo